export default function Home() {
  const [isPinMode, setIsPinMode] = useState(false);
  const [isConfirmMode, setIsConfirmMode] = useState(false); // NEW
  const [hasActivePin, setHasActivePin] = useState(false);
  const [radius, setRadius] = useState(5);

  const handleConfirm = () => {
    setIsConfirmMode(false);
    console.log("Location confirmed!");
    // You could trigger a search or save here
  };

  return (
    <div className="flex flex-col h-screen">
      <Sidebar 
        isPinMode={isPinMode}
        isConfirmMode={isConfirmMode} // Pass this
        onPinClick={() => setIsPinMode(true)}
        onConfirmClick={handleConfirm} // Pass this
        radius={radius}
        onRadiusChange={setRadius}
        hasActivePin={hasActivePin}
        onDeletePin={() => {
          setHasActivePin(false);
          setIsConfirmMode(false);
        }}
      />
      <GoogleMapsComponent 
        isPinMode={isPinMode}
        setPinMode={setIsPinMode}
        setIsConfirmMode={setIsConfirmMode} // Pass this to map
        searchRadius={radius}
        hasActivePin={hasActivePin}
        setHasActivePin={setHasActivePin}
      />
    </div>
  );
}