'use client';
import React, { useEffect, useRef, useState } from 'react';

const GOOGLE_MAPS_API_KEY = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '';

interface MapProps {
  isPinMode: boolean;
  setPinMode: (value: boolean) => void;
  searchRadius: number;
  hasActivePin: boolean;
  setHasActivePin: (value: boolean) => void;
}

export default function GoogleMapsComponent({ 
  isPinMode, 
  setPinMode, 
  searchRadius,
  hasActivePin,
  setHasActivePin 
}: MapProps) {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<google.maps.Map | null>(null);
  const currentMarkerRef = useRef<any>(null); 
  const currentCircleRef = useRef<google.maps.Circle | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);

  // 1. SCRIPT LOADER (unchanged)
  useEffect(() => {
    if (window.google?.maps) { setIsLoaded(true); return; }
    const script = document.createElement('script');
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAPS_API_KEY}&v=weekly&libraries=marker`;
    script.async = true;
    script.onload = () => setIsLoaded(true);
    document.head.appendChild(script);
  }, []);

  // 2. MAP INIT (unchanged)
  useEffect(() => {
    if (!isLoaded || !mapRef.current || mapInstanceRef.current) return;
    mapInstanceRef.current = new google.maps.Map(mapRef.current, {
      zoom: 13,
      center: { lat: 40.7200, lng: -73.8440 },
      mapId: 'DEMO_MAP_ID', 
    });
  }, [isLoaded]);

  // 3. THE SLIDER EFFECT: Updates existing circle radius in real-time
  useEffect(() => {
    if (currentCircleRef.current) {
      const meters = searchRadius * 1609.34;
      currentCircleRef.current.setRadius(meters);
      
      // Optional: Zoom map to fit the new radius size automatically
      // mapInstanceRef.current?.fitBounds(currentCircleRef.current.getBounds()!);
    }
  }, [searchRadius]); // Triggers every time the slider moves

  // 4. DELETE EFFECT: Wipes everything when delete button is pressed
  useEffect(() => {
    if (!hasActivePin) {
      if (currentMarkerRef.current) {
        currentMarkerRef.current.map = null;
        currentMarkerRef.current = null;
      }
      if (currentCircleRef.current) {
        currentCircleRef.current.setMap(null);
        currentCircleRef.current = null;
      }
    }
  }, [hasActivePin]);

// 5. PINNING LOGIC: Places the pin and makes it draggable
useEffect(() => {
  if (!mapInstanceRef.current || !isPinMode) return;
  const map = mapInstanceRef.current;
  map.setOptions({ draggableCursor: 'crosshair' });

  const clickListener = map.addListener('click', async (e: google.maps.MapMouseEvent) => {
    if (!e.latLng) return;

    // Cleanup previous objects
    if (currentMarkerRef.current) currentMarkerRef.current.map = null;
    if (currentCircleRef.current) currentCircleRef.current.setMap(null);

    const { AdvancedMarkerElement } = await google.maps.importLibrary("marker") as google.maps.MarkerLibrary;

    const pinElement = document.createElement('div');
    pinElement.className = 'bg-white text-blue-600 px-3 py-1 rounded-full border-2 border-blue-600 font-bold shadow-lg text-sm transition-transform active:scale-110 select-none cursor-grab active:cursor-grabbing';
    pinElement.innerText = '$ New';

    // 1. CREATE DRAGGABLE MARKER
    const newMarker = new AdvancedMarkerElement({
      map,
      position: e.latLng,
      content: pinElement,
      gmpDraggable: true, 
    });

    // 2. CREATE THE CIRCLE
    const newCircle = new google.maps.Circle({
      map,
      center: e.latLng,
      radius: searchRadius * 1609.34,
      fillColor: "#1a73e8",
      fillOpacity: 0.2,
      strokeColor: "#1a73e8",
      strokeWeight: 1,
      clickable: false, // Essential: prevents the circle from blocking the marker
    });

    // 3. UPDATED DRAG LOGIC
    // Using 'drag' ensures the circle moves in real-time as you move the mouse
    newMarker.addListener('drag', () => {
      const currentPos = newMarker.position;
      if (currentPos) {
        newCircle.setCenter(currentPos);
      }
    });

    currentMarkerRef.current = newMarker;
    currentCircleRef.current = newCircle;

    setHasActivePin(true);
    setPinMode(false);
    map.setOptions({ draggableCursor: null });
  });

  return () => google.maps.event.removeListener(clickListener);
}, [isPinMode, searchRadius, setPinMode, setHasActivePin]);

  return (
    <div className="w-full h-full relative">
      <div ref={mapRef} className="w-full h-full" />
    </div>
  );
}